<script>
  import { mapGetters } from 'vuex';
  import StarMap from './map/StarMap.vue';

  export default {
    components: {
      StarMap,
    },
    props: {
      numOfMap: {
        type: Number,
        require: true,
      },
    },
    data() {
      return {
        capturing: false,
        setImage: null,
        blob: null,
        wrapDiv: null,
        shapeClass: 'h-full',
      };
    },
    computed: {
      ...mapGetters('celestialMap', [
        'selectedOption',
        'getBg',
        'getMapShape',
        'getDetailsText',
        'getDesign',
        'getAlternativeFont',
      ]),
      ...mapGetters('mapObj', ['getCenter']),
    },
    mounted() {
      this.wrapDiv = this.$refs.wrap;
      this.checkRatio();
    },
    methods: {
      checkRatio() {
        if (this.wrapDiv) {
          if (this.wrapDiv[0].clientWidth > this.wrapDiv[0].clientHeight) {
            this.shapeClass = 'h-full';
          } else {
            this.shapeClass = 'w-full';
          }
        } else {
          this.shapeClass = '';
        }
      },
    },
    watch: {
      numOfMap(newVal) {
        if (newVal) {
          setTimeout(this.checkRatio, 999);
        }
      },
    },
  };
</script>

